import moment from "moment";
import {DataContext, DAY, FORMAT, PROFIT, WEEK} from "../../data/DataContext";
import React, {useContext} from "react";
import styles from "./week.module.css";

const WeekView = ({day = moment().format(FORMAT)}) => {
    const {getDaysOfRange, getTotal} = useContext(DataContext)
    const days = getDaysOfRange(day, WEEK)
    return <div className={styles.weekViewWrapper}>
        {days.map(date=>{
            return <WeekDay date={moment(date, FORMAT).format('DD')} weekday={moment(date, FORMAT).format('ddd')} profit={getTotal(DAY, PROFIT, date)}/>
        })}
    </div>

}

const WeekDay = ({date, weekday, profit}) => {

    return <div className={styles.weekDayWrapper}>
        <div className={styles.weekDayName}>{weekday}</div>
        <div className={styles.weekDayDate}>{date}</div>
        <div className={styles.weekDayProfit}>{profit}</div>
    </div>
}

export default WeekView
